<template>
  <div id="createRule">
    <v-icon title="创建角色" class="ml-4" v-text="icon" @click.stop="openCreateRuleDialog" />
    <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
      <v-card>
        <v-card-title>
          <span class="headline">创建角色</span>
        </v-card-title>

        <v-card-text>
          <v-form v-model="valid" :lazy-validation="lazy">
            <v-container>
              <v-row>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field v-model="role.name" autofocus :rules="nameRules" label="*角色名"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-select v-model="role.enabled" :items="enabledList" label="*启用"></v-select>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-textarea v-model="role.description" outlined label="描述" rows="3"></v-textarea>
                </v-col>
                <v-col cols="12" sm="12" md="12">选择权限</v-col>
                <v-col>
                  <v-treeview
                    :active.sync="active"
                    :items="permissions"
                    :open.sync="open"
                    color="warning"
                    return-object
                    hoverable
                    transition
                    selection-type="independent"
                    :selectable="selectable"
                    v-model="selections"
                  >
                    <template v-slot:prepend="{ open }">
                      <v-icon
                        :color="open ? 'green' : 'purple'"
                      >{{open ? 'mdi-file-tree-outline' : 'mdi-file-tree'}}</v-icon>
                    </template>
                  </v-treeview>
                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="closeCreateRuleDialog">取消</v-btn>
          <v-btn color="blue darken-1" text :disabled="!valid" @click="doCreateRole">保存</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { convert_tree } from "@/utils/tree";

export default {
  data: () => ({
    valid: true,
    lazy: false,
    icon: "mdi-plus-thick",
    dialog: false,
    role: {
      name: "",
      enabled: true,
      description: "",
      permissionIds: []
    },
    enabledList: [
      { text: "是", value: true },
      { text: "否", value: false }
    ],
    permissions: [],
    active: [],
    avatar: null,
    open: [],
    selectable: true,
    selections: [],
    nameRules: [v => !!v || "该选项为必填字段"]
  }),

  created() {},

  methods: {
    // 获取所有的权限
    getAllPermission() {
      this.$http
        .get("/user/api/v1/permission/listAll")
        .then(res => {
          this.permissions = convert_tree(res.data, null);
        })
        .catch(error => {});
    },

    // 打开对话框
    openCreateRuleDialog() {
      this.getAllPermission();
      this.dialog = true;

      // 每次打开窗口将role重置
      this.role.name = "";
      this.role.enabled = true;
      this.role.description = "";
      this.selections = [];
    },

    // 关闭对话框
    closeCreateRuleDialog() {
      this.dialog = false;
    },

    // 创建角色
    doCreateRole() {
      let params = { role: this.role, permissionIds: this.getPermissionIds() };
      this.$http
        .post("/user/api/v1/role/insert", params)
        .then(res => {
          if (res.success) {
            res.msg = "角色创建成功";
          }
          this.$store.commit("SET_SNACK", res);

          // 刷新角色列表
          this.$emit("getRoles");
        })
        .catch(error => {});

      this.closeCreateRuleDialog();
    },

    // 设置选中的权限
    getPermissionIds() {
      let permissionIds = [];
      permissionIds = this.selections.map(selection => selection.id);
      return permissionIds;
    }
  }
};
</script>