// 将平行的树节点组装成层级树节点
export function convert_tree(data, parentId) {
    parentId = parentId || null;
    var list = [];
    data.map(function (item) {
        if (item.parentId == parentId) {
            item.children = convert_tree(data, item.id);
            list.push(item);
        }
    });

    return list;
}